import config from '../config';
import sportConfig from '../utils/sportConfig';
import express from 'express';
import axios from 'axios';
import querystring from 'querystring';
axios.defaults.headers.common['JsonOdds-API-Key'] = config.apiKey;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
const router = express.Router();
import _ from 'lodash';
import BetOrder from '../models/BetOrder'
const OpenBet = BetOrder.OpenBet
import moment from 'moment';
import uniqid from 'uniqid';

router.post('/parlay-wager-submit', async (req, res) => {
	const latestParlayOddsWagerList = [];
	const reqBodyParlayEvents = []
	let orderExist = false

	req.body.parlayList.map(event => reqBodyParlayEvents.push(event.OddPickID))

	const existParlayOrders = await OpenBet.find({'user': req.user.username, 'betOption': 'Parlay'})
	existParlayOrders.map(parlayOrder => {
		const existParlayOrderEvents = []
		parlayOrder.events.map(event => existParlayOrderEvents.push(event.OddPickID))

		if(orderExist) return;
		if(_.difference(reqBodyParlayEvents, existParlayOrderEvents).length === 0){
			req.body.parlayList.map(event => {
				event.WagerDetail.Status = 'Existed'
				event.WagerDetail.ErrMsg = 'Order Existed ' + parlayOrder.orderNumber;
				event.WagerDetail.RiskAmount = 0;
				event.WagerDetail.WinAmount = 0;
				event.WagerDetail.WagerAmount = 0;
				event.WagerDetail.BetConfirm = false;
				latestParlayOddsWagerList.push(event)
			})
			orderExist = true;
		}
	})

	orderExist ? res.status(202).json(latestParlayOddsWagerList) :
	await Promise.all(req.body.parlayList.map(async (event) => {
		const queryOdd = querystring.stringify({GameIDs: event.ID, Source: event.OddDetail.SiteID, Sport: sportConfig[event.Sport].sport, OddType: event.BetDetail.OddType});
		const response = await axios.post('https://jsonodds.com/api/odds/bygames', queryOdd)

		switch(true){
			case _.isEmpty(response.data):
				event.WagerDetail.Status = 'NotFound'
				event.WagerDetail.ErrMsg = 'Event Not Found';
				event.WagerDetail.RiskAmount = 0;
				event.WagerDetail.WinAmount = 0;
				event.WagerDetail.WagerAmount = 0;
				event.WagerDetail.BetConfirm = false;
				delete event.PrevBetDetail;
				latestParlayOddsWagerList.push(event)
				break;
			case moment().utc('+07:00').isAfter(moment.parseZone(event.MatchTime+'+07:00').utc()):
				event.WagerDetail.Status = 'TimeOut'
				event.WagerDetail.ErrMsg = 'Event Time Out';
				event.WagerDetail.RiskAmount = 0;
				event.WagerDetail.WinAmount = 0;
				event.WagerDetail.WagerAmount = 0;
				event.WagerDetail.BetConfirm = false;
				delete event.PrevBetDetail;
				latestParlayOddsWagerList.push(event)
				break;
			default:
				event.OddDetail = response.data[0].Odds[0];
				event.PrevBetDetail = Object.assign({}, event.BetDetail);
				switch(true){
					case event.BetDetail.BetType === 'M-Line' && event.BetDetail.OddTarget === 'Home':
						 event.BetDetail.OddLine = event.OddDetail.MoneyLineHome;
						break;
					case event.BetDetail.BetType === 'M-Line' && event.BetDetail.OddTarget === 'Away':
						 event.BetDetail.OddLine = event.OddDetail.MoneyLineAway;
						break;
					case event.BetDetail.BetType === 'Spread' && event.BetDetail.OddTarget === 'Home':
						 event.BetDetail.OddPoint = event.OddDetail.PointSpreadHome;
						 event.BetDetail.OddLine = event.OddDetail.PointSpreadHomeLine;
						break;
					case event.BetDetail.BetType === 'Spread' && event.BetDetail.OddTarget === 'Away':
						 event.BetDetail.OddPoint = event.OddDetail.PointSpreadAway;
						 event.BetDetail.OddLine = event.OddDetail.PointSpreadAwayLine;
						break;
					case event.BetDetail.BetType === 'Total' && event.BetDetail.OddTarget === 'Over':
						 event.BetDetail.OddPoint = event.OddDetail.TotalNumber;
						 event.BetDetail.OddLine = event.OddDetail.OverLine;
						break;
					case event.BetDetail.BetType === 'Total' && event.BetDetail.OddTarget === 'Under':
						 event.BetDetail.OddPoint = event.OddDetail.TotalNumber;
						 event.BetDetail.OddLine = event.OddDetail.UnderLine;
						break;
					case event.BetDetail.BetType === 'Draw' && event.Sport === 7:
						 event.BetDetail.OddLine = event.OddDetail.DrawLine;
						break;
					default:
						return;
				}
				const quarter = (event.BetDetail.OddPoint % 1).toFixed(2)
				switch(true){
					case Number(event.BetDetail.OddLine) === 0:
						event.WagerDetail.Status = 'NotFound'
						event.WagerDetail.ErrMsg = 'Event Not Found';
						event.WagerDetail.RiskAmount = 0;
						event.WagerDetail.WinAmount = 0;
						event.WagerDetail.WagerAmount = 0;
						event.WagerDetail.BetConfirm = false;
						delete event.PrevBetDetail;
						latestParlayOddsWagerList.push(event)
						break;
					case Number(quarter) === 0.25 || Number(quarter) === 0.75:
						event.WagerDetail.Status = 'Quarter'
						event.WagerDetail.ErrMsg = 'Wager Currently Not Available';
						event.WagerDetail.RiskAmount = 0;
						event.WagerDetail.WinAmount = 0;
						event.WagerDetail.WagerAmount = 0;
						event.WagerDetail.BetConfirm = false;
						latestParlayOddsWagerList.push(event)
						break;
					case JSON.stringify(event.BetDetail) !== JSON.stringify(event.PrevBetDetail):
						event.WagerDetail.Status = 'HasUpdated'
						event.WagerDetail.ErrMsg = 'Wager Has Been Updated';
						event.WagerDetail.RiskAmount = 0;
						event.WagerDetail.WinAmount = 0;
						event.WagerDetail.WagerAmount = 0;
						event.WagerDetail.BetConfirm = false;
						latestParlayOddsWagerList.push(event)
						break;
					default:
						delete event.PrevBetDetail;
						latestParlayOddsWagerList.push(event)
						return;
				}
				return;
		}
	})).then(async () => {
		const allBetConfirmed = latestParlayOddsWagerList.every(event => event.WagerDetail.BetConfirm === true)
		if(allBetConfirmed){
			const newParlayOpenBet = new OpenBet({
				orderNumber: uniqid.process().toUpperCase(),
				superAgent: req.user.superAgent,
				betOption: 'Parlay',
				agent: req.user.agent,
				user: req.user.username,
				events: [],
				eventsWagerDetail: {
					ErrMsg: '',
					RiskAmount: req.body.riskAmount,
					WinAmount: req.body.winAmount,
					Result: ''
				}
			});
			latestParlayOddsWagerList.map(event => {
				event.Status = 'Pending';
				delete event.WagerDetail;
				delete event.PrevBetDetail;
				newParlayOpenBet.events.push(event)
			})
			await newParlayOpenBet.save()
			console.log('saved parpaly open bet')
			return res.status(200).send('saved a parlay open bet order')
		}else{
			return res.status(202).json(latestParlayOddsWagerList);
		}
	}).catch(error => res.status(404).send('parlay wager order submit error, unable save to database'))

})

export default router;











